import { trim } from './util.js'
export function idCardCheck(id) {
  id = trim(id)
  const city = ['11','12','13','14','15','21','22','23','31','32','33','34','35','36','37','41','42','43','44','45','46','50','51','52','53','54','61','62','63','64','65','71','81','82','91']
  if(!id) return false
  if(id && id.length!==18) return false
  if(city.indexOf(id.substr(0, 2)) === -1) return false
  let y = id.substr(6, 4)
  let m = id.substr(10,2)
  let d = id.substr(12,2)
  if(!new Date(y + '-' + m + '-' + d).getFullYear()) {
    return false
  }
  const reg = /(^\d{18}$)|(^\d{17}(\d|X|x)$)/
  return reg.test(id)
}