<template>
  <div class="realAuth-main">
    <c-nav-bar class="nav-bar" @clickRight="clickRight">
      <!-- <i slot="left" class="nav-bar-left iconfont icon-fanhui1"></i> -->
      <span slot="center" class="nav-bar-center">
        实名认证
      </span>
      <span slot="right" class="nav-bar-right">
        去首页
      </span>
    </c-nav-bar>
    <div class="realAuth-content" ref="content">
      <div>
        <div class="base-info">
          <div class="header">
            上传身份证照片
          </div>
          <div class="scan-content">
            <div class="scan-item">
              <c-scan-info class="scan-info"></c-scan-info>
              <p>上传身份证正面</p>
            </div>
            <div class="scan-item">
              <c-scan-info type="back" class="scan-info"></c-scan-info>
              <p>上传身份证反面</p>
            </div>
          </div>
        </div>
        <div class="name-id-date">
          <c-cell v-model="userName" title="姓名" :mark="true" rightType="input"></c-cell>
          <c-cell v-model="idcardNo" title="身份证号" :mark="true" rightType="input"></c-cell>
          <c-cell v-model="validDateEnd" title="有效止期" placeholder="有效止期(例如:2000-01-01)" :mark="true" rightType="input"></c-cell>
        </div>
        <div class="submit-btn">
          <van-button size="large" @click="_submit" class="btn" type="default">
            下一步
          </van-button>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import { idCardCheck } from '@/js/idcard.js'
import { trim } from '@/js/util.js'
import { isRealTime } from '@/js/time.js'
export default {
  data() {
    return {
      userName: '',
      idcardBackImg: '',
      idcardFrontImg: '',
      idcardNo: '',
      validDateEnd: '',
      scroll: ''
    }
  },
  created() {
  },
  mounted() {
    setTimeout(() => {
      this.scroll = new BScroll(this.$refs.content,{
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
    },20)
  },
  methods: {
    _submit() {
      this.$router.push('/realAuth/faceRecongnition')
      if(this.check()) {
        let { userName, idcardBackImg, idcardFrontImg, idcardNo, validDateEnd } = this
        let _params = {
          userName,
          idcardBackImg,
          idcardFrontImg,
          idcardNo,
          validDateEnd
        }
        this.$http.post('/api/userIdentity/submit',_params).then(res => {
          if (res.data.success){
            console.log(res)
            this.$router.push('/realAuth/faceRecongnition')
          } else {
            this.$toast(res.data.errorMessage)
          }
        }, err => {
          console.log(err)
        });
      }
    },
    check() {
      if(!this.idcardFrontImg) {
        this.$toast('请上传身份证正面')
        return false
      }
      if(!this.idcardBackImg) {
        this.$toast('请上传身份证反面')
        return false
      }
      const reg = /^[\u4E00-\u9FA5]{2,4}$/
      let userName = reg.test(trim(this.userName))
      if(!userName || userName.length<2) {
        this.$toast('姓名不正确')
        return false
      }
      if(!this.idCardCheck(this.idcardNo)) {
        this.$toast('请输入正确的身份证号')
        return false
      }
      if(!isRealTime(this.validDateEnd)) {
        this.$toast('请输入正确的有效止期')
        return false
      }
      return true
    },
    clickSelectBaseInfo() {},
    idCardCheck,
    clickRight() {
      console.log('------------')
      this.$router.push('/home')
    }
  }
}
</script>
<style lang="scss" scoped>
@import '~@/css/mixin.scss';
.realAuth-main {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgb(245,245,245);
  .nav-bar{
    background: #fff;
    z-index: 100;
  }
  .realAuth-content {
    position: absolute;
    top: 44px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    .base-info {
      margin-top:10px;
      background: rgb(255,255,255);
      padding: 0 15px;
      .scan-content {
        display: flex;
        justify-content: space-between;
        .scan-item {
          p {
            font-size: 14px;
            color:rgb(77,77,77);
            text-align: center;
            padding: 16px 0
          }
        }
      }
      .scan-info {
        width: 45vw;
        height: 30vw;
      }
    }
    .header{
      font-size: 16px;
      color: rgb(26,26,26);
      font-family: 'PingFang-SC-Bold';
      height: 56px;
      line-height: 56px;
      padding: 0 15px;
      background: rgb(255,255,255);
      font-weight: bold;
    }
    .name-id-date {
      position: relative;
      background: #fff;
      margin-top: 10px;
      & .cell-main:first-child::before {
        display: none
      }
    }
    .submit-btn {
      padding: 0 15px;
      margin-top: 40px;
      height: 45px;
      overflow: hidden;
      margin-bottom: 123px;
      .btn{
        border-radius: 22px;
        color: #fff;
        font-size: 18px;
        height: 44px;
        line-height: 44px;
        @include gradient();
      }
    }
  }
}
</style>
